import auth from '@/api/auth'

const state = {
    user:null,
    isLogin:false
}

const getters = {
    user: state => state.user,
    isLogin:state => state.isLogin
}

const mutations = {
    setUser(state,payload) {
        state.user = payload.user
    },

    setLogin(state,payload) {
        state.isLogin = payload.isLogin
    }
}

const actions = {
    login({commit},{username,password}) {//返回一个promise对象，之后就可以then
        return auth.login({username,password})
            .then(res => {
                commit('setUser',{user: res.data})// 把通过 axios 获取回来的用户数据提交 mutation，更新到 state：
                commit('setLogin',{isLogin:true})
            })    
    },

    //async作为一个关键字放在函数的前面，表示该函数是一个异步函数，意味着该函数的执行不会阻塞后面代码的执行 异步函数的调用跟普通函数一样
    //await即等待，用于等待一个Promise对象。它只能在异步函数 async function中使用，否则会报错,它的返回值不是Promise对象而是Promise对象处理之后的结果
    async register({commit},{username,password}) {
        let res = await auth.register({username,password})
        commit('setUser',{user:res.data})
        commit('setLogin',{isLogin:true})
        return res.data
    },

    async logout({commit}) {
        await auth.logout()
        commit('setUser',{user:null})
        commit('setLogin',{isLogin:false})
    },

    async checkLogin({commit,state}) {
        if(state.isLogin) return true //如果登录了，return true；否则，获取登录信息并commit
        let res = await auth.getInfo() //获取登录信息
        commit('setLogin',{isLogin:res.isLogin})
        if(!res.isLogin) return false //如果没有登录，return false;否则commit
        commit('setUser',{user:res.data})
        return true
    
    }

}

export default{
    state,
    getters,
    mutations,
    actions

}


